@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&family=Passion+One:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);

* {
  box-sizing: border-box;
}

body {
  font-family: "Lato", sans-serif;
  font-family: "Passion One", sans-serif;
  margin: 0;
  letter-spacing: 0.6px;
}

/* .active {
  background-color: white;
  color: #8ac5fd;
} */

nav {
  min-height: 20%;
  top: 0;
  padding: 1rem 7% 0 7%;
  /* position: sticky; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
}

.logo {
  width: 100px;
  height: auto;
}

ol {
  margin: 0;
  padding: 0;
  display: flex;
  grid-gap: 2rem;
  gap: 2rem;
  list-style: none;
  font-size: 18px;
  align-items: center;
}

ol li a {
  text-decoration: none;
  display: block;
  color: black;
  /* padding: 0.5rem 1rem;
  border-radius: 25px; */
}

ol li a:hover {
  color: #38b6ff;
}

.nav-tab {
  position: absolute;
  display: none;
  font-size: 20px;
  flex-direction: column;
  justify-content: space-between;
  top: 2rem;
  right: 10%;
}

.nav-tab:hover {
  color: #38b6ff;
}

#coupon-btn a {
  background-color: #38b6ff;
  color: white;
  border: 2px solid #38b6ff;
  border-radius: 15px;
  padding: 5px 10px;
  letter-spacing: 0.5px;
}

#coupon-btn a:hover {
  background-color: white;
  color: #38b6ff;
}

@media (max-width: 800px) {
  .nav-tab {
    display: flex;
    margin-top: 1rem;
  }

  nav {
    flex-direction: column;
    align-items: start;
  }

  ol {
    display: none;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
    padding: 1rem 0 1rem 0;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    background-color: white;
    color: white;
  }

  ol.navigation {
    display: flex;
  }

  ol li {
    width: 100%;
    text-align: center;
  }

  ol li a {
    text-decoration: none;
    display: block;
  }
}

