.hero {
  padding: 2rem 7%;
}

.hero-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-gap: 2rem;
  gap: 2rem;
}

.hero-info span {
  color: #38b6ff;
}

.hero-info h1 {
  font-size: 70px;
  margin: 0;
  text-align: center;
  margin-bottom: 1rem;
}

.hero-info div {
  display: flex;
  justify-content: center;
}

.hero-info a {
  text-decoration: none;
  background-color: #38b6ff;
  border: 2px solid #38b6ff;
  color: white;
  font-size: 20px;
  padding: 15px;
  border-radius: 20px;
}

.hero-info a:hover {
  background-color: white;
  color: #38b6ff;
}

.hero-img img {
  width: 100%;
}

@media (min-width: 1000px) {
  .hero-container {
    flex-direction: row;
    justify-content: space-between;
    padding: 6rem 0;
  }

  .hero-info {
    width: 50%;
  }

  .hero-info h1 {
    font-size: 80px;
    text-align: left;
  }

  .hero-info div {
    display: flex;
    justify-content: left;
  }

  .hero-img {
    width: 50%;
  }

  .hero-img img {
    width: 100%;
  }
}

.values {
  background-color: #38b6ff;
  display: flex;
  justify-content: center;
  padding: 3rem 10%;
}

.values-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* padding: 1rem 10%; */
  width: 100%;
}

.values-container h2 {
  text-align: center;
  letter-spacing: 0.7px;
  color: white;
}

.value-cards {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

@media (min-width: 1000px) {
  .values-container h2 {
    font-size: 30px;
  }

  .value-cards {
    flex-direction: row;
    justify-content: space-between;
    grid-gap: 2rem;
    gap: 2rem;
  }
}

.card-container {
  background-color: white;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1rem 0;
  border-radius: 25px;
  padding: 3rem 0;
}

.icon {
  color: #80e8b0;
  font-size: 60px;
}

.card-container h3 {
  font-size: 25px;
}

.card-container p {
  font-size: 18px;
  font-weight: 100;
  font-family: "Lato", sans-serif;
  color: #606060;
  text-align: left;
  width: 75%;
  margin: 0;
  padding-bottom: 2rem;
}

.card-container a {
  color: #38b6ff;
  list-style: none;
  text-decoration: none;
  font-size: 18px;
}

.card-container a:hover {
  color: lightgray;
}

@media (min-width: 1000px) {
  .card-container {
    height: 80%;
  }

  .card-container h3 {
    font-size: 20px;
  }

  .card-container p {
    margin-bottom: 2rem;
    font-size: 18px;
    padding-bottom: 0;
  }

  .card-container a {
    margin-bottom: 2rem;
    color: #38b6ff;
    list-style: none;
    text-decoration: none;
    font-size: 18px;
  }
}

.reviews {
  padding: 2rem 10%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.reviews h2 {
  text-align: center;
}

.reviews h2 span {
  color: #38b6ff;
  display: block;
}

.customer-reviews iframe {
  width: 100%;
  min-height: 500px;
  border: 0;
}

@media (min-width: 900px) {
  .reviews h2 span {
    display: inline;
  }
}

.about h1 {
  font-size: 70px;
  text-align: center;
  margin: 0;
  margin-top: 1rem;
  padding: 2rem 7% 2rem 7%;
  color: white;
  background-color: #38b6ff;
}

.intro-container {
  padding: 2rem 7% 2rem 7%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.intro-container h3 {
  font-size: 25px;
  padding: 1rem 2rem;
  border-bottom: 3px solid #38b6ff;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.intro-container p {
  font-size: 18px;
  font-weight: 100;
  font-family: "Lato", sans-serif;
  color: #606060;
}

.other-info {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 0 7% 2rem 7%;
}

.other-info div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.other-info h3 {
  font-size: 25px;
  padding: 0 2rem 1rem 2rem;
  border-bottom: 3px solid #38b6ff;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.other-img img {
  width: 400px;
}

.other-desc {
  font-size: 18px;
  font-weight: 100;
  font-family: "Lato", sans-serif;
  color: #606060;
  text-align: center;
  display: flex;
  flex-direction: column;
}

@media (min-width: 1000px) {
  .other-info div {
    flex-direction: row;
    justify-content: space-between;
    grid-gap: 3rem;
    gap: 3rem;
  }

  .other-img img {
    width: 500px;
  }

  .other-desc {
    width: 50%;
    text-align: left;
  }

  .other-desc p {
    display: block;
  }
}

.services {
  font-size: 20px;
}

.services h1 {
  font-size: 70px;
  text-align: center;
  margin: 0;
  margin-top: 1rem;
  padding: 2rem 7% 2rem 7%;
  color: white;
  background-color: #38b6ff;
}

.dry-laundry-container {
  padding: 2rem 7% 2rem 7%;
  background-color: white;
}

.service-container {
  display: flex;
  flex-direction: column-reverse;
}

.service-container-left {
  display: flex;
  flex-direction: column-reverse;
}

.service-img {
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.service-img img {
  width: 100%;
}

.service-header {
  padding-bottom: 1rem;
  border-bottom: 3px solid #38b6ff;
}

.service-header-left {
  padding-bottom: 1rem;
  border-bottom: 3px solid white;
}

.service-header,
.service-header-left {
  text-align: center;
}

.service-desc {
  font-size: 18px;
  font-weight: 100;
  font-family: "Lato", sans-serif;
  color: #606060;
}

.p-alt {
  color: black;
}

.item-list {
  display: block;
  list-style: inside;
  color: #606060;
}

.item-list-left {
  display: block;
  list-style: inside;
  color: black;
}

.alteration-container {
  padding: 2rem 7% 2rem 7%;
  background-color: #38b6ff;
  color: black;
}

@media (min-width: 900px) {
  .service-container {
    flex-direction: row;
    justify-content: space-between;
  }

  .service-container-left {
    flex-direction: row-reverse;
    justify-content: space-between;
  }

  .service-img {
    width: 45%;
  }

  .service-header {
    text-align: left;
    font-size: 30px;
    padding-bottom: 1rem;
    border-bottom: 3px solid #38b6ff;
  }

  .service-header-left {
    text-align: left;
    font-size: 30px;
    padding-bottom: 1rem;
    border-bottom: 3px solid white;
  }

  .service-info {
    width: 50%;
  }
}

.location {
  font-size: 20px;
}

.location h1 {
  font-size: 70px;
  text-align: center;
  margin: 0;
  margin-top: 1rem;
  color: white;
  background-color: #38b6ff;
  padding: 2rem 7% 2rem 7%;
}

.location-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 7% 2rem 7%;
}

.location-info {
  width: 100%;
}

.contact-icon {
  color: #38b6ff;
}

.address {
  display: flex;
  justify-content: left;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}

.phone {
  display: flex;
  justify-content: left;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}

.open-times {
  padding: 1rem 0;
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
}

.days {
  text-align: left;
}

.days p {
  padding-bottom: 0.6rem;
  margin: 0;
}

.times {
  text-align: left;
}

.times p {
  padding-bottom: 0.5rem;
  margin: 0;
}

.time-details {
  font-size: 18px;
  font-weight: 100;
  font-family: "Lato", sans-serif;
  color: #606060;
}

.google-img {
  width: 450px;
}

@media (min-width: 975px) {
  .location-container {
    flex-direction: row;
  }

  .google-img {
    width: 450px;
  }
}

.coupon h1 {
  font-size: 70px;
  text-align: center;
  margin: 0;
  margin-top: 1rem;
  padding: 2rem 7% 2rem 7%;
  color: white;
  background-color: #38b6ff;
}

.coupon-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem 7%;
}

.coupon-container h3 {
  font-size: 25px;
  border-bottom: 3px solid #38b6ff;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  text-align: center;
  padding: 1rem 2rem;
  margin-bottom: 0;
}

.coupon-container p {
  font-size: 18px;
  font-weight: 100;
  font-family: "Lato", sans-serif;
  color: #606060;
  text-align: center;
}

.coupon-container img {
  width: 80%;
  border: 3px solid #38b6ff;
}

.download-btn {
  font-size: 40px;
  color: black;
  padding-bottom: 2rem;
}

.download-btn:hover {
  color: #38b6ff;
}

footer {
  min-height: 25vh;
  background-color: #38b6ff;
}

.footer-container {
  padding: 1rem 7%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}

.left-footer {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.footer-nav {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  list-style: none;
  grid-gap: 1rem;
  gap: 1rem;
  border-left: 3px solid white;
  text-align: left;
}

.footer-nav a {
  text-decoration: none;
  color: white;
}

.footer-nav a:hover {
  color: lightgray;
}

.right-footer {
  text-align: center;
  color: white;
}

